import _ from 'lodash'
import Utils from './utils'

let _dataHandleFilter = (data, configObject = {}, groupAttrName, type = 'flat') => {
  var filterType = configObject.filter_type || 'and'

  /**
   * 过滤掉匹配的值
   * @param  {[type]} filter [过滤的配置]
   * @param  {[type]} data   [数据]
   * @return {[type]}        [description]
   */
  function matchValueFilter (filter, fdata) {
    if (filterType === 'and') {
      var _result = fdata

      _.each(filter, function (v) {
        if (v.type !== 'array' || !v.type) {
          _result = _.filter(_result, function (vd) {
            return Utils.isMatchValue(vd[v.name], v.compare, v.value)
          })
        } else {
          _result = _.filter(_result, function (vd) {
            return _.find(vd[v.key], function (vf) {
              return Utils.isMatchValue(vf[v.name], v.compare, v.value)
            })
          })
        }
      })

      return _result
    }

    if (filterType === 'or') {
      var orResult = []

      _.each(filter, function (v) {
        if (v.type !== 'array' || !v.type) {
          orResult.push(_.filter(fdata, function (vd) {
            return Utils.isMatchValue(vd[v.name], v.compare, v.value)
          }))
        } else {
          orResult.push(_.filter(fdata, function (vd) {
            return _.find(vd[v.key], function (vf) {
              return Utils.isMatchValue(vf[v.name], v.compare, v.value)
            })
          }))
        }
      })

      return _.flatten(orResult)
    }
  }

  if (Utils.isFlatStructure(type)) {
    // 扁平结构
    data = matchValueFilter(configObject.data, data)
  } else {
    _.each(data, function (v) {
      v[groupAttrName] = matchValueFilter(configObject.data, v[groupAttrName])
    })
  }

  return data
}

export default _dataHandleFilter
