import _ from 'lodash'
import dayjs from 'dayjs'

let compMapFunction = {
  '=': (a, b) => {
    return a === b
  },

  '>': (a, b) => {
    return a > b
  },

  '<': (a, b) => {
    return a < b
  },

  '>=': (a, b) => {
    return a >= b
  },

  '<=': (a, b) => {
    return a <= b
  },

  '!=': (a, b) => {
    return a !== b
  },

  in: (a, b) => {
    if (_.isArray(b)) {
      return _.indexOf(b, a) > -1
    }
    throw '【data_filter中的compare配置为in时，value类型必须为array！】'
  },
  notin: (a, b) => {
    if (_.isArray(b)) {
      return _.indexOf(b, a) === -1
    }
    throw '【data_filter中的compare配置为in时，value类型必须为array！】'
  },
  between: (a, b) => {
    if (_.isArray(b) && b.length >= 2) {
      return a >= b[0] && a <= b[1]
    }
    throw '【data_filter中的compare配置为between时，value类型必须为array，且长度要大于2！】'
  },
  RegExp: (a, b) => {
    if (_.isRegExp(b)) {
      return b.test(a)
    }

    throw '【data_filter中的compare配置为RegExp时，value类型必须为正则表达式！】'
  },
  // b是用来比较的值
  like: (a, b) => {
    if (!_.isString(a) || !_.isString(b)) {
      return false
    }

    if (b.indexOf(a) === -1) {
      return false
    }
    return true
  }
}

let operateMapFunction = {
  '+': (a, b) => {
    return (a || 0) + (b || 0)
  },
  '-': (a, b) => {
    return (a || 0) - (b || 0)
  },
  '*': (a, b) => {
    return (a || 0) * (b || 0)
  },
  '/': (a, b) => {
    return (a || 0) / (b || 1)
  }
}

let firstLetterUpCase = str => {
  let _str = ''

  let strArray = str.split(' ')

  for (let i = 0; i < strArray.length; i++) {
    _str += strArray[i].substr(0, 1).toUpperCase() + strArray[i].substr(1)
  }

  return _str
}

let numberFormat = (value, fixed, precision) => {
  if (_.isNumber(value) && _.isNumber(+value) && _.isNumber(fixed)) {
    return value.toFixed(fixed)
  }

  if (_.isNumber(value) && _.isNumber(+value) && _.isNumber(precision)) {
    return value.toPrecision(precision)
  }
}

let isMatchValue = (value, compare = '=', matchValue) => {
  if (matchValue === 'null') {
    matchValue = null
  }

  if (matchValue === 'undefined') {
    matchValue = undefined
  }

  if (matchValue === 'NaN') {
    matchValue = NaN
  }

  if (_.isArray(matchValue) && _.indexOf(matchValue, 'undefined') > -1) {
    matchValue.push(undefined)
  }

  return compMapFunction[compare](value, matchValue)
}

let Utils = {
  /**
   * 配置节点
   * @param  {[type]}  config [配置]
   * @return {Boolean}        [是否存在该配置]
   */
  hasConfig: config => {
    let _configExist = config && !_.isEmpty(config)

    let _dataExist = config && config.data && !_.isEmpty(config.data)

    let _groupSortExist =
      config && config.group_sort && !_.isEmpty(config.group_sort)

    return _configExist && (_dataExist || _groupSortExist)
  },
  /**
   * 是否是扁平化结构
   * @param  {[type]}  type [数据结构类型]
   * @return {Boolean}      [description]
   */
  isFlatStructure: type => {
    if (type === 'flat') {
      return true
    }

    return false
  },

  /**
   * 分组后扁平化输出，排序无法对字符串排序，可以用分组的方法排序
   * @param  {[type]} data [description]
   * @param  {[type]} attr [description]
   * @return {[type]}      [description]
   */
  groupAndFlatData: (data, attr) => {
    let _result = []

    let _groupData = _.groupBy(data, function (v) {
      return v[attr]
    })

    for (let prop in _groupData) {
      _result.push(_groupData[prop])
    }

    return _.flatten(_result)
  },

  /**
   * 值匹配函数
   * @param  {[type]}  value      [元数据]
   * @param  {[type]}  compare    [比较符]
   * @param  {[number，string，array]}  matchValue [匹配值]
   * @return {Boolean}            [description]
   */
  isMatchValue: isMatchValue,

  isMatchCompare: (result, compareObject) => {
    let { type = 'and', data } = compareObject
    let matchResult = null
    _.each(data, v => {
      let { field, value, compare } = v
      let matchValue = isMatchValue(result[field], compare, value)
      matchResult = matchResult === null ? matchValue : (type === 'or' ? !!(matchResult || matchValue) : !!(matchResult && matchValue))
    })
    return matchResult
  },

  transFnName: configName => {
    if (!configName) {
      return
    }

    let splitName = configName.split('_').join(' ')

    return firstLetterUpCase(splitName)
  },

  /**
   * 值格式化
   * @param  {[type]} value [数值]
   * @param  {[type]} fixed [保留小数位]
   * @param  {[type]} format   [时间格式化]
   * @return {[type]}       [description]
   */
  dataValueFormat: (value, config) => {
    let { fixed, precision, format } = config

    let filterData = config.filter_data
    let copyName = config.copy_name

    // 在过滤范围内的不进行处理
    if (filterData && _.isArray(filterData) && filterData.indexOf(value) > -1) {
      return value
    }

    if (_.isNumber(value) && _.isNumber(+value) && _.isNumber(fixed)) {
      if (copyName) {
        return value.toFixed(fixed)
      }

      return value.toFixed(fixed)
    }

    if (_.isNumber(value) && _.isNumber(+value) && _.isNumber(precision)) {
      return value.toPrecision(precision)
    }

    if (value && format && _.isString(format)) {
      return dayjs(value).format(format)
    }

    if (_.isUndefined(value)) {
      return config.undefined_tip || ''
    }

    return value
  },

  /**
   * 值匹配函数
   * @param  {[type]}  config    [配置]
   * @param  {[type]}  data      [数据]
   * @return {Boolean}            [description]
   */
  isOperationValue: (config, data) => {
    let { operator, name, fixed, precision } = config

    let extremaVal = config.extrema_val
    let operatorName = config.operator_name

    let value = data[name]

    let optValue = _.isString(operatorName) ? data[operatorName] : operatorName

    let flag = true

    if (optValue === 'null') {
      optValue = null
    }

    if (optValue === 'undefined') {
      optValue = undefined
    }

    if (optValue === 'NaN') {
      optValue = NaN
    }

    if ((!optValue && optValue !== 0) || (!value && value !== 0)) {
      flag = false
    }

    return flag
      ? numberFormat(
        operateMapFunction[operator](value, optValue),
        fixed,
        precision
      )
      : extremaVal
  }
}
export default Utils
