import _ from 'lodash'
import DataSort from './sort'

const GROUPINGVIEW = [
  {
    field: 'province_name',
    emptyValue: '其他'
  },
  {
    field: 'city_name',
    emptyValue: '其他',
    currentLevel: {
      orignName: 'area_name',
      compareName: 'province_name',
      replaceName: '本级'
    }
  },
  {
    field: 'county_name',
    emptyValue: '其他',
    currentLevel: {
      orignName: 'area_name',
      compareName: 'city_name',
      replaceName: '本级'
    }
  },
  {
    field: 'town_name',
    emptyValue: '其他',
    currentLevel: {
      orignName: 'area_name',
      compareName: 'county_name',
      replaceName: '本级'
    }
  },
  {
    field: 'village_name',
    emptyValue: '其他',
    currentLevel: {
      orignName: 'area_name',
      compareName: 'town_name',
      replaceName: '本级'
    }
  }
]

const GROUPKEYCOPYTOPARENT = [
  'province_code',
  'city_code',
  'county_code',
  'town_code',
  'village_code'
]

const GROUPDATASORT = [
  {
    name: 'province_code',
    order: 'asc'
  },
  {
    name: 'city_code',
    order: 'asc'
  },
  {
    name: 'county_code',
    order: 'asc'
  },
  {
    name: 'town_code',
    order: 'asc'
  },
  {
    name: 'village_code',
    order: 'asc'
  }
]

let areaGroupConfig = {
  groupingView: [],
  groupKeyCopyToParent: [],
  groupDataSort: {
    data: []
  }
}

class main {
  constructor (data, config) {
    this._data = _.cloneDeep(data)
    if (config.groupTemplate && config.groupTemplate === 'area') {
      let minLevel = config.groupTemplateMinLevel || 'province'
      let nareaGroupConfig = this.defaultAreaGroup(minLevel)
      this._config = Object.assign(nareaGroupConfig, config)
    } else {
      this._config = config
    }
    this._groupExpand = this._config.groupExpand || []
    this._tempLevelFields = _.cloneDeep(this._config.groupingView || [])
    this._isExcludeParent = _.isBoolean(this._config.isExcludeParent)
      ? this._config.isExcludeParent
      : true
  }

  defaultAreaGroup (minLevel) {
    let nConfig = Object.assign({}, areaGroupConfig)

    for (let i = 0; i < GROUPINGVIEW.length; i++) {
      let v = GROUPINGVIEW[i]
      if (v.field.indexOf(minLevel) <= 0) {
        nConfig.groupingView.push(v)
      }
      if (v.field.indexOf(minLevel) === 0) {
        break
      }
    }

    for (let i = 0; i < GROUPKEYCOPYTOPARENT.length; i++) {
      let v = GROUPKEYCOPYTOPARENT[i]
      if (v.indexOf(minLevel) <= 0) {
        nConfig.groupKeyCopyToParent.push(v)
      }
      if (v.indexOf(minLevel) === 0) {
        break
      }
    }

    for (let i = 0; i < GROUPDATASORT.length; i++) {
      let v = GROUPDATASORT[i]
      if (v.name.indexOf(minLevel) <= 0) {
        nConfig.groupDataSort.data.push(v)
      }
      if (v.name.indexOf(minLevel) === 0) {
        break
      }
    }

    return nConfig
  }

  generateUUID () {
    var d = new Date().getTime()
    var uuid = 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (
      c
    ) {
      var r = (d + Math.random() * 16) % 16 | 0
      d = Math.floor(d / 16)
      return (c === 'x' ? r : (r & 0x3) | 0x8).toString(16)
    })
    return uuid
  }
  /**
   * 构建树形结构
   * @param {*} cusConfig 自定义配置 {dataSort: {}}
   */
  buildTree (cusConfig) {
    this._treeArray = []

    let data = this._data

    let result = []

    if (!data.length) {
      return []
    }

    if (!this._config.isUseParent) {
      this._getTreeGridData(data, 0)

      result = _.sortBy(_.flatten(this._treeArray), v => {
        return v.group_cid
      })

      if (this._config.groupDataSort) {
        result = this._buildTreeSorted(result)
      }
    } else {
      result = this._relyParentBuildTree(data, this._config, cusConfig)
      // if (this._config.groupDataSort) {
      //   let { groupDataSort = {} } = this._config

      //   let _sortBy = [
      //     groupDataSort.data || [],
      //     {
      //       name: 'group_cid',
      //       order: 'asc'
      //     }
      //   ]

      //   result = DataSort(this._treeArray, { data: _.flatten(_sortBy) })
      // }
    }

    return data && data.length ? result : []
  }

  _getTreeGridData (data, levelIndex) {
    let fieldOp = this._tempLevelFields[levelIndex]

    let groups = _.groupBy(data, v => {
      if (!v[fieldOp.field]) {
        v[fieldOp.field] = fieldOp.emptyValue
      }
      return v[fieldOp.field]
    })

    let excludeParentGroup = () => {
      let treeLevelFields = this._tempLevelFields

      this._tempLevelFields =
        treeLevelFields.length > 1
          ? _.slice(treeLevelFields, 1)
          : treeLevelFields

      fieldOp = this._tempLevelFields[levelIndex]

      groups = _.groupBy(data, v => {
        if (!v[fieldOp.field]) {
          v[fieldOp.field] = fieldOp.emptyValue
        }
        return v[fieldOp.field]
      })

      if (
        _.keys(groups).length === 1 &&
        _.keys(groups)[0] === fieldOp.emptyValue
      ) {
        excludeParentGroup()
      }
    }

    // 是否要处理不显示父级分组的处理（适用省市县通用版本，市县级数据不需要省级分组的情况）
    if (this._isExcludeParent) {
      if (
        _.keys(groups).length === 1 &&
        _.keys(groups)[0] === fieldOp.emptyValue
      ) {
        excludeParentGroup()
      }
    }

    let count = 0

    _.each(groups, (v, k) => {
      let parents = {
        group_guid: this.generateUUID(),
        group_name: k,
        group_childs: [],
        group_childs_length: 0,
        group_cid: 'v-' + this._countAddDigits(count),
        group_parent_cid: 0,
        group_level: levelIndex,
        is_leaf: false,
        all_child_length: v.length,
        is_expand: !count, // 默认第一级展开
        is_expand_show: true // 默认第一级显示
      }

      this._copyCustomAttrToParent(parents, v[0])

      let _pcdata = this._getExcludeCurrent(v, k)

      parents = Object.assign(parents, _pcdata.parents) // $.extend(true, {}, parents, _pcdata.parents)

      this._getChildsData(parents, _pcdata.childs, levelIndex + 1)

      this._treeArray.push(parents)

      count++
    })
  }

  _getExcludeCurrent (data, value) {
    let { currentLevelAttr } = this._config
    let parents = {}
    let childs = data
    if (currentLevelAttr) {
      parents =
        _.find(data, v => {
          return v[currentLevelAttr] === value
        }) || {}

      childs =
        _.filter(data, v => {
          return v[currentLevelAttr] !== value
        }) || data
    }
    return { parents, childs }
  }

  _getChildsData (parents, data, levelIndex) {
    let fieldOp = this._tempLevelFields[levelIndex]

    let notNextGroup = _.find(data, v => {
      return v.not_next_group
    })

    if (!fieldOp || notNextGroup) {
      parents.group_childs_length = data.length

      _.each(data, (v, k) => {
        v.is_leaf = true

        v.group_level = levelIndex

        v.group_cid = parents.group_cid + this._countAddDigits(k)

        v.group_parent_cid = parents.group_cid

        v.is_expand_show = parents.is_expand

        this._treeArray.push(v)
      })

      return
    }

    let groups = _.groupBy(data, v => {
      let currentLevel = fieldOp.currentLevel

      if (
        currentLevel &&
        v[currentLevel.orignName] === v[currentLevel.compareName]
      ) {
        v[fieldOp.field] = currentLevel.replaceName

        v.not_next_group = true
      } else if (!v[fieldOp.field]) {
        v[fieldOp.field] = fieldOp.emptyValue
      }
      return v[fieldOp.field]
    })

    parents.group_childs_length = _.keys(groups).length

    let count = 0

    _.each(groups, (v, k) => {
      let childs = {
        group_guid: this.generateUUID(),
        group_name: k,
        group_childs: [],
        group_childs_length: 0,
        group_cid: parents.group_cid + this._countAddDigits(count),
        group_parent_cid: parents.group_cid,
        group_level: levelIndex,
        is_leaf: false,
        all_child_length: v.length,
        is_expand: false,
        is_expand_show: levelIndex === 1 // 第一层默认展开
      }

      this._copyCustomAttrToParent(childs, v[0])

      let _pcdata = this._getExcludeCurrent(v, k)

      childs = Object.assign(childs, _pcdata.parents) // $.extend(true, {}, childs, _pcdata.parents)

      this._treeArray.push(childs)

      this._getChildsData(childs, _pcdata.childs, levelIndex + 1)

      count++
    })
  }

  _countAddDigits (count) {
    let defStr = '00000'
    let countLen = count.toString().length
    return defStr.substr(countLen - defStr.length) + count
  }

  _relyParentBuildTree (data = [], config, cusConfig = {}) {
    if (!data.length) {
      return []
    }

    let { key = 'id', parentKey = 'parent_id', groupName = 'name', groupDataSort = {} } = config

    // let rootValue = config.rootParentValue || '0' // 根节点值

    let result = []

    _.each(data, v => {
      let pValue = v[parentKey] && v[parentKey].toString().trimRight()

      let npCode = _.find(data, d => {
        return pValue === d[key].toString().trimRight()
      })

      if (!npCode) {
        result.push(v)
      }
    })

    result = _.flatten(result)

    if (groupDataSort) {
      let cusDataSort = cusConfig.dataSort
      result = DataSort(result, cusDataSort || groupDataSort)
    }

    if (!result.length) {
      // throw '未找到根节点数据，怎么进行树形构建'
    }

    let newResult = []

    let count = 0

    let childGroupBuild = (dataArray, parent) => {
      _.each(dataArray, (v, k) => {
        // 判断是否是最后一级的根节点
        count = parent.group_level + 1 > count ? parent.group_level + 1 : count
        v.group_level = parent.group_level + 1
        v.group_name = v[groupName]
        v.group_cid = parent.group_cid + this._countAddDigits(k)
        v.group_parent_cid = parent.group_cid
        v.group_childs = _.filter(data, d => {
          return (
            d[parentKey] &&
            d[parentKey].toString().trimRight() === v[key].trimRight()
          )
        })
        v.group_childs_length = v.group_childs.length

        v.group_guid = this.generateUUID()
        v.is_leaf = !v.group_childs_length
        v.is_expand = !v.group_level && !k
        v.is_expand_show = parent.is_expand || !v.group_level
        v.all_child_length = v.group_childs.length

        newResult.push(v)

        if (v.group_childs_length) {
          childGroupBuild(v.group_childs, v)
        }
      })
    }

    childGroupBuild(result, {
      group_level: -1,
      group_cid: 'v-'
    })

    if (this._config.totalAllChilds) {
      _.each(newResult, v => {
        let regexp = new RegExp('^' + v.group_cid)

        v.group_childs_length =
          _.filter(newResult, d => {
            return regexp.test(d.group_cid)
          }).length - 1
      })
    }

    return newResult
  }

  _buildTreeSorted (data, config) {
    let ndata = []

    let temp = Object.assign([], data) // $.extend(true, [], data)

    let { groupDataSort = {} } = this._config

    let fdata = [_.min(data, 'group_level')]

    function pushChildNodeBySorted (childData) {
      let sdata = DataSort(childData, groupDataSort) // DataHandle._dataHandleSorted(['flat', _sortConfig || {}, childData]);

      _.each(sdata, v => {
        if (!v.isPush) {
          v.isPush = true

          ndata.push(v)

          let regexp = new RegExp('^' + v.group_cid + '.')

          let cfdata = _.filter(temp, d => {
            return regexp.test(d.group_cid)
          })

          if (cfdata.length) {
            pushChildNodeBySorted(cfdata)
          }
        }
      })
    }

    pushChildNodeBySorted(fdata)

    return ndata
  }

  _copyCustomAttrToParent (parent, data) {
    let { groupKeyCopyToParent = [] } = this._config

    if (groupKeyCopyToParent.length && data) {
      _.each(groupKeyCopyToParent, d => {
        parent[d] = data[d]
      })
    }
    return parent
  }
}

export default main
