import _ from 'lodash'
import Utils from './utils'
import Handles from './handles'

let _dataHandleComplex = (data, configObject = {}, groupAttrName, configName, type = 'flat') => {
  var fnName = 'fn' + Utils.transFnName(configName)

  if (Utils.isFlatStructure(type)) {
    _.each(data, function (v) {
      v = Handles[fnName](configObject.data, v)
    })
  } else {
    _.each(data, function (v) {
      _.each(v[groupAttrName], function (vf) {
        vf = Handles[fnName](configObject.data, vf)
      })
    })
  }

  return data
}

export default _dataHandleComplex
