import _ from 'lodash'
import Utils from './utils'

let Handles = {
  fnDataReplace: (config, data) => {
    _.each(config, function (v) {
      let { name, replaceOtherName, value, replaceVal, type } = v

      let dataVal = data[name]

      let replaceName = replaceOtherName || name

      _.each(value, function (vf) {
        if (type === 'RegExp' && dataVal) {
          data[replaceName] = dataVal.replace(new RegExp(vf), replaceVal)
        } else {
          if (dataVal === vf) {
            data[replaceName] = replaceVal
          }

          if (dataVal === undefined && vf === 'undefined') {
            data[replaceName] = replaceVal
          }
        }
      })
    })

    return data
  },

  fnDataCombine: (config, data) => {
    _.each(config, function (vc) {
      let _str = ''

      let _delimiter = vc.delimiter || ''

      let _copyName = vc.copy_name

      _.each(vc.data, function (vcd, k) {
        if (k) {
          _str += _delimiter
        }

        let _val = Utils.dataValueFormat(data[vcd.name], vcd)

        let _unitVal =
          _val || _val === 0 ? (vcd.unit ? _val + vcd.unit : _val) : _val

        _str += _unitVal
      })

      data[_copyName] = _str
    })

    return data
  },

  fnDataFormat: (config, data) => {
    _.each(config, function (v) {
      let _val = Utils.dataValueFormat(data[v.name], v)
      if (
        v.exclude &&
        v.exclude.length &&
        _.indexOf(v.exclude, data[v.name]) > -1
      ) {
        _val = data[v.name]
      }
      let noUnit = v.noUnit
      let _unitVal = _val || _val === 0 ? (v.unit ? _val + v.unit : _val) : _val
      if (_.isArray(noUnit)) {
        if (
          _.find(noUnit, function (noVal) {
            return noVal === _val
          })
        ) {
          _unitVal = _val
        }
      }
      if (v.copy_name) {
        data[v.copy_name] = _unitVal
      } else {
        data[v.name] = _unitVal
      }
    })
    return data
  },

  fnDataFollowTip: (config, data) => {
    _.each(config, function (v) {
      let name = v.name
      let followName = v.follow_name || name
      let copyName = v.copy_name

      _.each(v.data, function (vf) {
        let _isMatch = Utils.isMatchValue(data[name], vf.compare, vf.value)

        let _value =
          (data[followName] || data[followName] === 0 ? data[followName] : '') +
          vf.follow_value

        if (_isMatch && !copyName) {
          data[followName] = _value
        } else if (_isMatch && copyName) {
          data[copyName] = _value
        }
      })
    })

    return data
  },

  fnDataCalculate: (config, data) => {
    _.each(config, function (v) {
      if (v.copy_name) {
        data[v.copy_name] = Utils.isOperationValue(v, data)
      }
    })

    return data
  },

  fnDataAddAttributes: (config, data) => {
    _.each(config, function (v) {
      let { name, compare } = v
      if (!compare) {
        _.each(v.data, function (vf) {
          let _isMatch = Utils.isMatchValue(data[name], vf.compare, vf.value)
          _.each(vf.data, function (vd) {
            let addName = vd.add_name
            let addValue = vd.add_value

            if (_isMatch && addName && addValue) {
              data[addName] = addValue
            }
          })
        })
      } else {
        let _isMatchValue = Utils.isMatchCompare(data, compare)
        _.each(v.data, function (vd) {
          let addName = vd.add_name
          let addValue = vd.add_value

          if (_isMatchValue && addName && addValue) {
            data[addName] = addValue
          }
        })
      }
    })
    return data
  },

  fnDataCopy: (config, data) => {
    _.each(config, function (v) {
      let name = v.name
      let copyName = v.copy_name

      if (copyName && !data[copyName]) {
        data[copyName] = data[name]
      }
    })

    return data
  }
}
export default Handles
