import _ from 'lodash'
/**
 * @param {*} masterData 主接口数据
 * @param {*} mergeDatas 需要合并到主接口的接口数据，是个数组，可以邮多批数据
 * @param {*} config
 * @param {*} type type为schema处理不对外提供
 * {
 *    "master": {
        "relate": "id",
        "properties": "copy_name"   //存在，所有的合并数据都合并到这个字段上， 不存在则复制到源数据上
      },
      "merge": [{
        "relate": "id",
        "properties": [{   //存在，只合并里面列的内容，不存在则全部合并
          "key": "type",
          "reKey": "type"
        },
        {
          "key": "data",
          "reKey": "rings"
        },
        {
          "key": "layer_type",
          "reKey": "layer_type"
        }]
      }]
 * }
 */
let _dataHandleMerge = (masterOrigin, mergeDatas, config, type) => {
  let { master, merges } = config
  let masterData = JSON.parse(JSON.stringify(masterOrigin))
  _.each(mergeDatas, (v, k) => {
    if (type === 'schema') {
      masterData = _dataCombineSchema(
        masterData,
        v,
        master.properties,
        merges[k] && merges[k].properties
      )
    } else {
      masterData = _dataCombine(
        masterData,
        v,
        master.relate,
        master.properties,
        merges[k] && merges[k].relate,
        merges[k] && merges[k].properties
      )
    }
  })
  return masterData
}

let _dataCombineSchema = (master, data, masterProperties, dataProperties) => {
  let nCopyData = {}
  if (dataProperties) {
    // 将配置的属性值复制到数据中
    _.each(dataProperties, d => {
      if (d.reKey) {
        nCopyData[d.reKey] = data[d.key]
      } else {
        nCopyData[d.key] = data[d.key]
      }
    })
  } else {
    nCopyData = Object.assign(nCopyData, data)
  }
  if (masterProperties) {
    master[masterProperties] = nCopyData
  } else {
    master = Object.assign(master, nCopyData)
  }
  return master
}

let _dataCombine = (
  master,
  data,
  masterRelate = 'id',
  masterProperties,
  dataRelate = 'id',
  dataProperties
) => {
  _.each(master, function (v) {
    var fd = _.find(data, d => {
      if (!d) {
        return false
      }
      return v[masterRelate] === d[dataRelate]
    })
    let nCopyData = {}
    if (fd) {
      if (dataProperties) {
        // 将配置的属性值复制到数据中
        _.each(dataProperties, d => {
          if (d.reKey) {
            nCopyData[d.reKey] = fd[d.key]
          } else {
            nCopyData[d.key] = fd[d.key]
          }
        })
      } else {
        nCopyData = Object.assign(nCopyData, fd)
      }
      if (masterProperties) {
        v[masterProperties] = nCopyData
      } else {
        v = Object.assign(v, nCopyData)
      }
    }
  })
  return master
}

export default _dataHandleMerge
