import Vue from 'vue'
/**
 * [LayerManager 图层管理对象，所有图层信息]
 * @type {Object}
 */
const LayerManager = {}

const MapUtil = function (options) {
  this.init(options)
}
MapUtil.prototype = {

  /**
   * [init 初始化操作]
   * @param  {[type]} options [description]
   * @return {[type]}              [description]
   */
  init: function (options) {
    this.options = options || {}
    // 当前页面上下文，用于地图弹出框
    this.context = this.options.context
    if (!this.theme) {
      this.theme = 'default'
    }
    // 图层缓存,图层信息数组
    this.layerConfigs = {
      count: 0 // 图层数量
    }
    this.getLayerConfig(this.options.config)
    // 矢量图层集合，结构：{layerId: {layer: layer对象, geometries:图元集合对象}
    this.vectoryLayers = {
      count: 0 // 图层数量
    }
    // 瓦片图层集合，结构：{layerId: {layer: layer对象, geometries:图元集合对象}
    this.tileLayers = {
      count: 0 // 图层数量
    }
    this.shpLayers = {
      count: 0
    }
    // 图片图层集合，结构：{layerId: {layer: layer对象, geometries:图元集合对象}
    this.imageLayers = {
      count: 0 // 图层数量
    }
  },

  /**
   *  [getLayerConfig 获取当前命中菜单模块的指定layerId的图层配置，layerId为图层编号或者索引]
   * @param  {[number]} layerId [图层编号或者索引]
   * @return {[type]}          [图层配置，json结构：结构：{
      "layerId": '1',//图层编号，数值，唯一，必须
      "type": "vectoryPoints",//图片图层
      "isGeojson": false,//是否是geojson结构的数据，默认为false
      "apiMethod": "",//api方法名称，对应api.json中的method配置
      "apiParams": {},//接口的参数
      "data": [],//静态数据
      "labelData": [], //标注数据
      "options": {},//默认的图元配置信息，如：zoom、minZoom、maxZoom、样式等
      "labelOptions": {}//默认标注配置信息
  }]
  */
  getLayerConfig: function (layerId, func) {
    layerId = layerId || null
    if (layerId === null) {
      return []
    }
    var typeStr = typeof (layerId)
    if (typeStr === 'object' && !Array.isArray(layerId)) {
      layerId = [layerId]
    }
    if (!Array.isArray(layerId) && typeStr !== 'string' && typeStr !== 'number') {
      return []
    }
    if (typeStr === 'string') {
      layerId = layerId.split(',')
    }
    if (typeStr === 'number') {
      layerId = [layerId.toString()]
    }
    if (layerId.length === 0) {
      return []
    }
    let that = this
    let layers = that.layerConfigs
    return layerId.map(d => {
      let lid = null
      if (typeof (d) === 'object') {
        lid = that.addLayerConfig(d)
      } else {
        lid = d
      }
      if (typeof (func) === 'function') {
        func(layers[lid])
      }
      return layers[lid]
    })
  },

  /**
   * [_addLayerConfig 添加图层配置]
   * @param  {[object]} config [图层配置]
   * @return {[type]}        [description]
   */
  addLayerConfig: function (config) {
    let allLayers = this.layerConfigs
    // 如果未配置layerId，则使用数组下标作为layerId值
    let layerId = config.layerId = config.layerId || allLayers.count.toString()
    config.properties = config.properties || {}
    if (config.properties.feature) {
      config.properties.feature.geoType = config.type
    } else {
      config.properties.feature = {
        geoType: config.type
      }
    }
    config.labelProperties = config.labelProperties || {}
    if (config.labelProperties.feature) {
      config.labelProperties.feature.geoType = 'Text'
    } else {
      config.labelProperties.feature = {
        geoType: 'Text'
      }
    }
    allLayers[layerId] = config
    allLayers.count++
    return layerId
  },

  getMap: function () {
    let map = this.options.map
    if (!map) {
      throw new Error('缺少map参数，无法进行地图相关操作！')
    }
    return map
  },

  /**
   * [addLayer 根据传入的图层配置添加图层]
   * @param {[object|array]} config [图层配置信息，结构：{
    "layerId": '1',//图层编号，数值，唯一，必须
      "type": "vectoryPoints",//图片图层
      "isGeojson": false,//是否是geojson结构的数据，默认为false
      "apiMethod": "",//api方法名称，对应api.json中的method配置
      "apiParams": {},//接口的参数
      "data": [],//静态数据
      "labelData": [], //标注数据
      "options": {},//默认的图元配置信息，如：zoom、minZoom、maxZoom、样式等
      "labelOptions": {}//默认标注配置信息
  }]
  */
  addLayer: function (config) {
    config = this.getLayerConfig(config)
    let len = config.length
    let temp
    for (let i = 0; i < len; i++) {
      temp = config[i]
      switch (temp.type) {
        case 'vectoryPoint':
          this._addVectorLayer(temp, temp.isCluster === true ? 'addClusterPoints' : temp.isCluster2 === true ? 'addClusterPoints2' : 'addPoints')
          break
        case 'vectoryPolyline':
          this._addVectorLayer(temp, 'addPolylines')
          break
        case 'vectoryPolygon':
          this._addVectorLayer(temp, 'addPolygons')
          break
        case 'tile':
          this.addTileLayer(temp)
          break
        case 'shp':
          this.addShpLayer(temp)
          break
        case 'image':
          this.addImageLayer(temp)
          break
      }
    }
  },

  /* ----------------------------------------------------矢量相关操作-start---------------------------------------------------------------- */
  /**
   * [addVectorPoints 添加矢量点图层，支持根据业务接口扁平化数据转为geojson结构，支持接口直接返回geojson结构的数据]
   * @param {[object]} config [图层配置信息，结构：{
          "isGeojson": false,//是否是geojson结构的数据，默认为false
          "isCluster": false,//是否做聚合效果
          "apiMethod": "",//api方法名称，对应api.json中的method配置
          "apiParams": {},//接口的参数
          "data": [],//静态数据
          "labelData": [], //标注数据
          "options": {},//默认的图元配置信息，如：zoom、minZoom、maxZoom、样式等
          "labelOptions": {}//默认标注配置信息
        }]
   */
  addVectorPoints: function (config) {
    config = this.getLayerConfig(config)
    let len = config.length
    let temp
    for (let i = 0; i < len; i++) {
      temp = config[i]
      this._addVectorLayer(temp, temp.isCluster === true ? 'addClusterPoints' : 'addPoints')
    }
  },

  /**
   * [addVectorPolylines 添加矢量线图层，支持根据业务接口扁平化数据转为geojson结构，支持接口直接返回geojson结构的数据]
   * @param {[object]} config [图层配置信息，结构：{
          "isGeojson": false,//是否是geojson结构的数据，默认为false
          "apiMethod": "",//api方法名称，对应api.json中的method配置
          "apiParams": {},//接口的参数
          "data": [],//静态数据
          "labelData": [], //标注数据
          "options": {},//默认的图元配置信息，如：zoom、minZoom、maxZoom、样式等
          "labelOptions": {}//默认标注配置信息
        }]
   */
  addVectorPolylines: function (config) {
    config = this.getLayerConfig(config)
    let len = config.length
    let temp
    for (let i = 0; i < len; i++) {
      temp = config[i]
      this._addVectorLayer(temp, 'addPolylines')
    }
  },

  /**
   * [addVectorPolygons 支持根据业务接口扁平化数据转为geojson结构，支持接口直接返回geojson结构的数据]
   * @param {[object]} config [图层配置信息，结构：{
      "isGeojson": false,//是否是geojson结构的数据，默认为false
          "apiMethod": "",//api方法名称，对应api.json中的method配置
          "apiParams": {},//接口的参数
          "data": [],//静态数据
          "labelData": [], //标注数据
          "options": {},//默认的图元配置信息，如：zoom、minZoom、maxZoom、样式等
          "labelOptions": {}//默认标注配置信息
        }]
   */
  addVectorPolygons: function (config) {
    config = this.getLayerConfig(config)
    let len = config.length
    let temp
    for (let i = 0; i < len; i++) {
      temp = config[i]
      this._addVectorLayer(temp, 'addPolygons')
    }
  },

  /**
   * [_addVectorLayer 添加矢量数据图层，私有函数]
   * @param {[object]} config [图层配置，结构：{
      "isGeojson": {
        "data": true,
        "labels": true,
        "geojsonFile": true,
        "apiMethod": true
      },//是否是geojson结构的数据，默认为false
      "apiMethod": "",//api方法名称，对应api.json中的method配置
      "apiParams": {},//接口的参数
      "data": [],//静态数据
      "labels": [], //标注数据
      "properties": {},//默认的图元配置信息，如：zoom、minZoom、maxZoom、样式等
      "labelProperties": {}//默认标注配置信息
      }]
   * @param {[string]} funcKey [调用map的相关方法名称，可选值：addPoints、addPolylines, addPolygons]
  */
  _addVectorLayer: function (config, funcKey) {
    let map = this.getMap()
    let layer = map.layer()
    if (funcKey === null || typeof (layer[funcKey]) !== 'function') {
      return
    }
    let vectoryLayers = this.vectoryLayers
    let obj = {
      layer: layer,
      geometries: []
    }
    let isGeojson = Object.assign({
      data: false,
      labels: false,
      apiMethod: false,
      geoFile: false,
      isEncode: false
    }, config.isGeojson || {})
    let layerId = config.layerId

    vectoryLayers[layerId] = obj
    LayerManager[layerId] = obj
    vectoryLayers.count++

    // 1、静态矢量数据
    let data = config.data || []
    let props = config.properties || {}
    let labels = config.labels || []
    let labelProps = config.labelProperties || {}
    let that = this

    this._bindGeoEvents(layer, config, {
      click: (props.callback || {}).click || function (e, config) {
        that._defClickCallback(e, config)
      }
    })
    // layer.on('click', clickCallback)

    let apiMethod = config.apiMethod || null
    let params = Object.assign({
      // layerId: layerId
    }, config.apiParams)
    let isEncode = params.isEncode || isGeojson.isEncode || false
    // 1.1 添加几何图元
    this._addGeoToLayer(obj, funcKey, isGeojson.data, isEncode, data, props)
    // 1.2 添加文本标注
    this._addGeoToLayer(obj, 'addTexts', isGeojson.labels, isEncode, labels, labelProps)

    // 2、请求接口的矢量数据
    if (apiMethod !== null && apiMethod.length > 0 && typeof (Vue.$api[apiMethod]) === 'function') {
      Vue.$api[apiMethod]({
        params: params
      }).then(function (resp) {
        resp = (resp || {}).data || {}
        // 2.1 添加几何图元
        data = isGeojson.apiMethod ? resp : resp.data
        data = data || []
        that._addGeoToLayer(obj, funcKey, isGeojson.apiMethod, isEncode, data, props)
        // 2.2 添加文本标注
        labels = resp.labels || []
        that._addGeoToLayer(obj, 'addTexts', isGeojson.apiMethod, isEncode, labels, labelProps)
      })
    }

    // 3、添加json文件的矢量数据图层
    let geoFile = config.geoFile || ''
    if (geoFile.length > 0) {
      Vue.$api.get(geoFile).then(function (resp) {
        resp = resp || {}
        // 3.1 添加几何图元
        data = isGeojson.geoFile ? resp : resp.data
        data = data || []
        that._addGeoToLayer(obj, funcKey, isGeojson.geoFile, isEncode, data, props)
        // 3.2 添加文本标注
        labels = resp.labels || []
        that._addGeoToLayer(obj, 'addTexts', isGeojson.geoFile, isEncode, labels, labelProps)
      })
    }
  },

  /**
   * [_addGeoToLayer 添加几何图元到图层]
   * @param {[type]}  layerInfo [description]
   * @param {Boolean} isGeojson [description]
   * @param {Boolean} isEncode [description]
   * @param {[type]}  data      [description]
   * @param {[type]}            [description]
   */
  _addGeoToLayer: function (layerInfo, funcKey, isGeojson, isEncode, data, properties) {
    if (isGeojson && !Array.isArray(data) && Array.isArray(data.geometries)) {
      this._geojsonDataToLayer(layerInfo, isEncode, data, properties)
    } else if (Array.isArray(data) && data.length > 0) {
      this._arrayDataToLayer(layerInfo, funcKey, data, properties)
    }
  },

  /**
   * [_geojsonDataToLayer 添加geojson图元到图层]
   * @param  {[type]}  layerInfo  [description]
   * @param  {Boolean} isEncode   [description]
   * @param  {[type]}  data       [description]
   * @param  {[type]}  properties [description]
   * @return {[type]}             [description]
   */
  _geojsonDataToLayer (layerInfo, isEncode, data, properties) {
    properties = properties || {}
    // 1、配置文件中的图元样式、标注样式。
    let pData = properties.data || []
    let pFeature = properties.feature || {}
    let pTooltip = properties.tooltip || {}
    let arr = data.geometries || []
    let that = this
    let geos = []
    arr.forEach((g) => {
      // 2、数据中的图元样式、标注样式。
      let gProps = g.properties || {}
      let gFeature = gProps.feature || {}
      let gTooltip = gProps.tooltip || {}
      let gData = gProps.data || {}
      if (gData.length) {
        gData = gData[0]
      }
      let extendData = pData.find(pd => {
        return pd.idno === gFeature.idno
      }) || {}
      // 3、场景样式, 根据extendData数据中指定字段的匹配指定值来获取个性的图元样式、标注样式.
      let cProps = that._caseProperties(extendData, properties.case)
      let cFeature = cProps.feature || {}
      // let tag = cFeature.tag || gData.tag || extendData.tag || gFeature.tag || pFeature.tag
      // let iconSrc = cFeature.iconSrc || cFeature.src || gData.iconSrc || gData.src || extendData.iconSrc || extendData.src || gFeature.iconSrc || gFeature.src || pFeature.iconSrc || pFeature.src || 'circle'
      // iconSrc = iconSrc.replace('{theme}', this.theme || 'default')
      // iconSrc.endsWith('.jpg') || iconSrc.endsWith('.png') || iconSrc.endsWith('.gif') ? 'img' : null
      // 4、图元样式优先级：1 < 2 < 3
      gProps.feature = Object.assign({}, pFeature, gFeature, extendData, gData, cFeature, {
        // src: iconSrc,
        // tag: tag,
        icon: cFeature.icon || gData.icon || extendData.icon || gFeature.icon || pFeature.icon || {
          iconSize: cFeature.iconSize || gData.iconSize || extendData.iconSize || gFeature.iconSize || pFeature.iconSize || [16, 16]
        }
      })
      // 5、标注样式优先级：1 < 2 < 3
      gProps.tooltip = Object.assign({}, pTooltip, gTooltip, cProps.tooltip)
      gProps.popup = cProps.popup
      g.properties = Object.assign(gProps, extendData)
      if (isEncode) {
        let encodeCoords = g.encodeCoords || []
        g.coordinates = []
        encodeCoords.forEach((c) => {
          g.coordinates.push(that.decodeCoordinates(c.encode, c.encodeOffset))
        })
      }
    })
    geos = layerInfo.layer.addGeoJSON(data, properties)

    if (geos.length > 0) {
      layerInfo.geometries = layerInfo.geometries.concat(geos)
      geos.forEach((g) => {
        if (g && g[0]) {
          g[0].bringToFront()
          that._bindGeoEvents(g[0], properties, {
            mouseover: that._defMouseoverCallback,
            mouseout: that._defMouseoutCallback
          })
        }
      })
    }
  },

  /**
   * [_arrayDataToLayer 添加数组图元到图层]
   * @param  {[type]} layerInfo  [图层信息]
   * @param  {[type]} funcKey    [description]
   * @param  {[type]} data       [description]
   * @param  {[type]} properties [description]
   * @return {[type]}            [description]
   */
  _arrayDataToLayer (layerInfo, funcKey, data, properties) {
    properties = properties || {}
    // 1、配置文件中的图元样式、标注样式。
    let pData = properties.data || []
    let pCluster = properties.cluster || {}
    let pFeature = properties.feature || {}
    let pTooltip = properties.tooltip || {}
    let geos = []
    let that = this
    let gData = data.map(d => {
      return Object.assign({}, pData.find(pd => {
        return pd.idno === d.idno
      }), Array.isArray(d) && d.length === 2 ? {
        coordinates: d
      } : d)
    })
    geos = layerInfo.layer[funcKey](gData, d => {
      let cProps = that._caseProperties(d, properties.case)
      let cFeature = cProps.feature || {}
      let cCluster = cProps.cluster || {}
      let tag = cFeature.tag || d.tag || pFeature.tag
      let iconSrc = cFeature.iconSrc || cFeature.src || d.iconSrc || d.src || pFeature.iconSrc || pFeature.src || 'circle'
      iconSrc = iconSrc.replace('{theme}', this.theme || 'default')
      // iconSrc.endsWith('.jpg') || iconSrc.endsWith('.png') || iconSrc.endsWith('.gif') ? 'img' : null
      d.popup = cProps.popup
      return {
        feature: Object.assign({}, pFeature, d, cFeature, {
          src: iconSrc,
          tag: tag,
          icon: cFeature.icon || d.icon || pFeature.icon || {
            iconSize: cFeature.iconSize || d.iconSize || pFeature.iconSize || [16, 16]
          }
        }),
        cluster: Object.assign({
          zoomToBoundsOnClick: true,
          zoomToMaxBoundsOnClick: true,
          spiderfyOnMaxZoom: false,
          maxClusterRadius: 50,
          disableClusteringAtZoom: 8,
          showCoverageOnHover: false
        }, pCluster, cCluster),
        tooltip: Object.assign({}, pTooltip, d, cProps.tooltip)
      }
    })
    if (geos.length > 0) {
      layerInfo.geometries = layerInfo.geometries.concat(geos)
      geos.forEach((g) => {
        if (g) {
          that._bindGeoEvents(g, properties, {
            mouseover: that._defMouseoverCallback,
            mouseout: that._defMouseoutCallback
          })
        }
      })
    }
  },

  /**
   * [_bindGeoEvents 绑定图元事件]
   * @param  {[type]} geo        [图元对象]
   * @param  {[type]} properties [属性配置信息]
   * @return {[type]}            [description]
   */
  _bindGeoEvents (geo, properties, callback) {
    callback = Object.assign(callback, properties.callback)
    for (let prop in callback) {
      if (typeof (callback[prop]) === 'function') {
        geo.on(prop, e => {
          callback[prop](e, properties)
        })
      }
    }
  },
  /**
   * [_defClickCallback 图元点击事件默认回调函数]
   * @param  {[type]} e          [description]
   * @param  {[type]} properties [description]
   * @return {[type]}            [description]
   */
  _defClickCallback (e, config) {
    let properties = config.properties || {}
    let geo = e.layer
    let data = geo.__data || {}
    let dProps = data.properties || {}
    if (properties.popup || data.propup || dProps.popup) {
      let pConfig = Object.assign({}, properties.popup, data.popup, dProps.popup)
      geo.openPopup()
      this._openPopup(data, pConfig)
    }
  },
  /**
   * [_geoMouseoverCallback 图元鼠标移入事件默认回调函数]
   * @param  {[type]} e [description]
   * @return {[type]}   [description]
   */
  _defMouseoverCallback (e, properties, highlight) {
    let target = e.target || {}
    let hoverFeature = null
    if (highlight) {
      hoverFeature = Object.assign({
        fillColor: 'orange',
        color: 'orange',
        fillOpacity: 0.8
      }, properties.hoverFeature, target.__data.hoverFeature)
    } else if (properties.hoverFeature || target.__data.hoverFeature) {
      hoverFeature = Object.assign({}, properties.hoverFeature, target.__data.hoverFeature)
    } else {
      return
    }
    target.__data.hoverFeature = hoverFeature
    target.__data.normalFeature = Object.assign({}, target.options)
    if (hoverFeature && typeof (target.setStyle) === 'function') {
      target.setStyle(hoverFeature)
    }
  },
  /**
   * [_geoMouseoutCallback 图元鼠标移出事件默认回调函数]
   * @param  {[type]} e [description]
   * @return {[type]}   [description]
   */
  _defMouseoutCallback (e) {
    let target = e.target
    let normalFeature = target.__data.normalFeature
    if (normalFeature && typeof (target.setStyle) === 'function') {
      target.setStyle(normalFeature)
    }
  },

  /**
   * [_caseFeature 根据properties.caseProperties配置获取图元、图标、弹出框配置]
   * @param  {[type]} data        [description]
   * @param  {[type]} caseFeature [description]
   * @return {[type]}             [description]
   */
  _caseProperties (data, caseProperties) {
    caseProperties = caseProperties || []
    let props = {}
    if (data && caseProperties.length > 0) {
      caseProperties.forEach((item) => {
        let field = item.field || []
        let value = item.value || []
        let len = field.length
        let isMapped = false
        for (let i = 0; i < len; i++) {
          let f = field[i]
          let dVal = data[f]
          let targetVal = value[i]
          isMapped = Array.isArray(targetVal) ? targetVal.indexOf(dVal) > -1 : targetVal === dVal
          if (isMapped) {
            props.feature = Object.assign({}, props.feature, item.feature)
            props.tooltip = Object.assign({}, props.tooltip, item.tooltip)
            props.popup = props.popup || item.popup ? Object.assign({}, props.popup, item.popup) : null
            break
          }
        }
      })
    }
    return props
  },
  /**
   * [decodeCoordinates 解压缩矢量数据]
   * @param  {[type]} coordinate   [description]
   * @param  {[type]} encodeOffset [description]
   * @return {[type]}              [description]
   */
  decodeCoordinates: function (encode, encodeOffset, encodeScale = 1024) {
    var result = []
    if (!encodeOffset || !encode) {
      return result
    }
    var prevX = encodeOffset[0]
    var prevY = encodeOffset[1]

    for (var i = 0; i < encode.length; i += 2) {
      var x = encode.charCodeAt(i) - 64
      var y = encode.charCodeAt(i + 1) - 64
      // ZigZag decoding
      x = (x >> 1) ^ (-(x & 1))
      y = (y >> 1) ^ (-(y & 1))
      // Delta deocding
      x += prevX
      y += prevY

      prevX = x
      prevY = y
      // Dequantize
      result.push([x / encodeScale, y / encodeScale])
    }

    return result
  },

  /**
   * [toggleGeometries 显示隐藏指定图层的图元]
   * @param  {[number|array]} layerIds [图层编号或者索引]
   * @param  {[bool]} display [显示隐藏标志，true/false]
   * @return {[type]}         [description]
   */
  toggleGeometries: function (display) {
    let that = this
    let layerConfigs = that.layerConfigs
    let vectoryLayers = that.vectoryLayers
    for (let key in layerConfigs) {
      let l = layerConfigs[key] || {}
      if (key !== 'count' && l.toggleGeometries !== false) {
        that._toggleGeometries(vectoryLayers[key], display)
      }
    }
  },

  /**
   * [setGeometriesVisible 设置指定图层指定图元的显示隐藏]
   * @param {[type]} layerInfo [description]
   * @param {[type]} display   [description]
   * @param {[type]} data      [description]
   */
  setGeometriesVisible: function (data, display) {
    data = data || []
    let that = this
    let vectoryLayers = that.vectoryLayers
    data.forEach((d) => {
      that._toggleGeometries(vectoryLayers[d.layerId], display, d.data)
    })
  },

  /**
   * [_toggleGeometries 显示隐藏指定图层的图元]
   * @param  {[type]} layerInfo [图层对象，this.vectoryLayers缓存的对象]
   * @param  {[bool]} display [显示隐藏标志，true/false]
   * @return {[type]}           [description]
   */
  _toggleGeometries: function (layerInfo, display, data) {
    data = data || []
    layerInfo = layerInfo || {}
    let layer = layerInfo.layer || null
    if (layer === null) {
      return
    }
    let geos = layerInfo.geometries || []
    if (!display) {
      if (data.length > 0) {
        data.forEach((d) => {
          if (geos[d.geoIndex]) {
            geos[d.geoIndex].removeFrom(layer.map)
          }
        })
      } else {
        geos.forEach((g) => {
          g.removeFrom(layer.map)
        })
      }
    } else {
      if (data.length > 0) {
        data.forEach((d) => {
          if (geos[d.geoIndex]) {
            geos[d.geoIndex].addTo(layer.map)
          }
        })
      } else {
        geos.forEach((g) => {
          g.addTo(layer.map)
        })
      }
    }
  },

  /**
   * [toggleTooltip 显示隐藏指定图层的图元标注]
   * @param  {[number|array]} layerIds [图层编号或者索引]
   * @param  {[bool]} display [显示隐藏标志，true/false]
   * @return {[type]}         [description]
   */
  toggleTooltip: function (display) {
    let that = this
    let layerConfigs = that.layerConfigs
    let vectoryLayers = that.vectoryLayers
    for (let key in layerConfigs) {
      let l = layerConfigs[key] || {}
      if (key !== 'count' && l.toggleTooltip !== false) {
        that._toggleTooltip(vectoryLayers[key], display)
      }
    }
  },

  _toggleTooltip: function (layerInfo, display) {
    layerInfo = layerInfo || {}
    let layer = layerInfo.layer || null
    if (layer === null) {
      return
    }
    // let geos = layerInfo.geometries || []
    if (!display) {
      layer.setTooltipOptions({
        visible: false
      })
      // geos.forEach((g, i) => {
      //   // 文本图元
      //   if (g.options.geoType === 'Text') {
      //     g.removeFrom(layer.map)
      //   } else {
      //     g.hideTooltip(true)
      //   }
      // })
    } else {
      layer.setTooltipOptions({
        visible: true
      })
      // geos.forEach((g, i) => {
      //   // 文本图元
      //   if (g.options.geoType === 'Text') {
      //     g.addTo(layer.map)
      //   } else {
      //     g.hideTooltip(false)
      //   }
      // })
    }
  },

  /**
   * [toPosition 地图定位]
   * @return {[type]} [description]
   */
  toPosition: function (data, opt) {
    data = data || {}
    opt = opt || {}
    let map = this.getMap()
    let center = []
    let zoom = opt.zoom || map.getZoom()
    let posByLnglat = true
    if (Array.isArray(data) && data.length === 2) {
      center = data
    } else {
      data.lng = data.lng || data.lon
      if (!data || !data.lng || !data.lat) {
        if (Array.isArray(data.coordinates) && data.coordinates.length === 2) {
          data.lng = data.coordinates[0]
          data.lat = data.coordinates[1]
        } else {
          posByLnglat = false
        }
      }
      zoom = data.zoom || zoom
      center = [data.lng, data.lat]
    }

    if (posByLnglat) {
      if (opt.animation !== false) {
        map.setView(center, zoom)
      } else {
        map.setCenterZoom(center, zoom)
      }
    }
    // 高亮定位效果，结合悬停配置hoverFeater进行高亮定位
    if (opt.highlight !== false) {
      let layerConfigs = this.getLayerConfig(opt.layerId) || []
      let that = this
      layerConfigs.forEach((c) => {
        if (c) {
          let layerInfo = this.vectoryLayers[c.layerId] || null
          if (layerInfo) {
            let geos = layerInfo.geometries || []
            let props = c.properties || {}
            let targetGeo = geos.find(g => {
              if (!g) {
                return false
              }
              let gOpt = g.options || (g[0] || {}).options || {}
              let idno = gOpt.idno || gOpt.id || null
              if (!idno) {
                let gData = g.__data || (g[0] || []).__data || {}
                let fea = (gData.properties || {}).feature || {}
                let bData = (gData.properties || {}).data || {}
                idno = gData.idno || gData.id || bData.idno || bData.id || fea.idno || fea.id
              }
              return idno === data.idno || idno === data.id
            })
            if (targetGeo) {
              if (Array.isArray(targetGeo)) {
                targetGeo = targetGeo[0]
              }
              let count = 0
              let timeInterval = setInterval(function () {
                let flag = count % 2 === 0
                count++
                if (count > 5) {
                  clearInterval(timeInterval)
                }
                try {
                  if (flag) {
                    that._defMouseoverCallback({
                      target: targetGeo
                    }, props, true)
                  } else {
                    that._defMouseoutCallback({
                      target: targetGeo
                    }, props, true)
                  }
                } catch (e) {
                  window.console.log('高亮定位报错')
                  window.console.log(e)
                }
              }, 300)
              if (!posByLnglat) {
                map.setExtent(targetGeo, zoom)
              }
            }
          }
        }
      })
    }
  },

  /**
   * [toPopup 仅弹出框]
   * @param  {[type]} layerId [description]
   * @param  {[type]} data    [description]
   * @param  {[type]} opt     [description]
   * @param  {[type]} context [description]
   * @return {[type]}         [description]
   */
  toPopup: function (layerId, data, opt, context) {
    opt = opt || {}
    let configs = this.getLayerConfig(layerId)
    let vectoryLayers = this.vectoryLayers
    let that = this
    if (configs.length === 0) {
      for (let key in vectoryLayers) {
        let layerInfo = vectoryLayers[key]
        if (layerInfo && layerInfo.layer) {
          let geo = layerInfo.geometries.find(g => {
            if (!g) {
              return false
            }
            let gOpt = g.options || (g[0] || {}).options || {}
            let idno = gOpt.idno || gOpt.id || null
            if (!idno) {
              let gData = g.__data || (g[0] || []).__data || {}
              let fea = (gData.properties || {}).feature || {}
              let bData = (gData.properties || {}).data || {}
              idno = gData.idno || gData.id || bData.idno || bData.id || fea.idno || fea.id
            }
            return idno === data.idno || idno === data.id
          })
          if (geo) {
            if (Array.isArray(geo)) {
              geo = geo[0]
            }
            geo.openPopup()
            let gData = geo.__data || (geo[0] || []).__data || {}
            let fea = (gData.properties || {}).feature || {}
            let popup = Object.assign({}, fea.popup, opt)
            that._openPopup(data, popup, context)
          }
        }
      }
    } else {
      configs.forEach((c) => {
        let layerInfo = vectoryLayers[c.layerId]
        if (layerInfo && layerInfo.layer) {
          let geo = layerInfo.geometries.find(g => {
            if (!g) {
              return false
            }
            let gOpt = g.options || (g[0] || {}).options || {}
            let idno = gOpt.idno || gOpt.id || null
            if (!idno) {
              let gData = g.__data || (g[0] || []).__data || {}
              let fea = (gData.properties || {}).feature || {}
              let bData = (gData.properties || {}).data || {}
              idno = gData.idno || gData.id || bData.idno || bData.id || fea.idno || fea.id
            }
            return idno === data.idno || idno === data.id
          })
          if (geo) {
            if (Array.isArray(geo)) {
              geo = geo[0]
            }
            geo.openPopup()
            let gData = geo.__data || (geo[0] || []).__data || {}
            let fea = (gData.properties || {}).feature || {}
            let cFea = (c.properties || {}).feature || {}
            let popup = Object.assign({}, cFea.popup, fea.popup, opt)
            that._openPopup(data, popup, context)
          }
        }
      })
    }
  },

  _openPopup: function (data, popupOpt, context) {
    if (popupOpt.isLink === true) {
      let url = popupOpt.linkUrl || ''
      let params = popupOpt.linkUrlParams || {}
      if (url.length > 0) {
        for (let k in params) {
          url = url.replace(['{', k, '}'].join(''), data[params[k]] || '')
        }
        window.open(url)
      }
    } else {
      context = context || this.context || null
      if (context !== null) {
        // Popup.closeAll()
        // Popup.open(data, popupOpt, null, context)
      }
    }
  },

  /**
   * [toPosPopup 定位并弹出框]
   * @param  {[type]} data    [description]
   * @param  {[type]} opt     [description]
   * @param  {[type]} context [description]
   * @param  {[type]} layerId [description]
   * @return {[type]}         [description]
   */
  toPosPopup: function (layerId, data, opt, context) {
    opt = opt || {}
    this.toPosition(data, Object.assign({
      layerId
    }, opt))
    this.toPopup(layerId, data, opt.popup, context)
  },

  /**
   * [toAnchored 锚定操作]
   * @param  {[type]} data [图元数据，必须包含经纬度信息，支持[lng, lat], {lng:, lon:}2种结构]
   * @param  {[type]} opt  [可选参数，{zoom:缩放视野，未配置或者设置为0表示不进行缩放, src: '锚定图标路径，未配置使用内部默认的', size: [图标大小，width, height]}。]
   * @return {[type]}      [description]
   */
  toAnchored: function (data, opt) {
    data = data || {}
    opt = opt || {}
    let anchoredLayer = this.anchoredLayer || null
    if (anchoredLayer === null) {
      this.anchoredLayer = anchoredLayer = {
        layer: null,
        geometries: []
      }
    }
    this.removeAnchored()
    if (!anchoredLayer.layer) {
      let map = this.getMap()
      anchoredLayer.layer = map.layer()
    }
    opt.animation = false
    this.toPosition(data, opt)
    // 添加锚定图标
    let iconSrc = data.iconSrc || data.src || opt.iconSrc || opt.src || '/theme/{theme}/assets/map-util/icon_anchored.png'
    iconSrc = iconSrc.replace('{theme}', this.theme || 'default')
    let tag = iconSrc.endsWith('.jpg') || iconSrc.endsWith('.png') || iconSrc.endsWith('.gif') ? 'img' : null
    let iconSize = data.iconSize || opt.iconSize || [22, 32]
    iconSize = Array.isArray(iconSize) ? iconSize : [iconSize, iconSize]
    let anchoredPoint = anchoredLayer.layer.addPoints(data, () => {
      return {
        feature: {
          tag: tag,
          src: iconSrc,
          bounce: !!opt.bounce,
          icon: {
            iconAnchor: tag === 'img' ? [11, iconSize[1]] : [0, iconSize[1] / 2],
            iconSize: iconSize
          }
        }
      }
    })
    anchoredLayer.geometries.push(anchoredPoint)
    if (tag === 'img' && !!opt.bounce) {
      let bounceCount = 0
      anchoredLayer.intervalId = setInterval(function () {
        bounceCount++
        anchoredPoint[0].refreshIconOptions({
          iconAnchor: bounceCount % 2 === 0 ? [11, iconSize[1]] : [11, iconSize[1] + 8 + Math.random() * 5]
        })
      }, 300)
    }
    return anchoredLayer
  },

  /**
   * [removeAnchored 移除锚定]
   * @param  {[type]} data [description]
   * @param  {[type]} opt  [description]
   * @return {[type]}      [description]
   */
  removeAnchored: function () {
    let anchoredLayer = this.anchoredLayer || null
    if (anchoredLayer === null || !anchoredLayer.layer) {
      return
    }
    anchoredLayer.layer.remove()
    if (anchoredLayer.intervalId) {
      clearInterval(anchoredLayer.intervalId)
    }
    anchoredLayer.layer = null
    anchoredLayer.intervalId = null
    anchoredLayer.geometries = []
  },

  /* ----------------------------------------------------矢量图层相关操作-end---------------------------------------------------------------- */

  /* ----------------------------------------------------瓦片图层相关操作-start---------------------------------------------------------------- */
  /**
   * [addTileLayer 叠加瓦片图层，图包]
   * @param {[object]} config   [图层配置，结构说明同addLayer函数一致]
   */
  addTileLayer: function (config) {
    config = this.getLayerConfig(config)
    let len = config.length
    let temp
    for (let i = 0; i < len; i++) {
      temp = config[i]
      this._addTileLayer(temp)
    }
  },

  /**
   * [_addTileLayer 叠加瓦片图层，图包]
   * @param {[object]} config   [图层配置，结构说明同addLayer函数一致]
   */
  _addTileLayer: function (config) {
    let map = this.getMap()
    if (map === null) {
      return
    }
    let apiMethod = config.apiMethod || 'mapProvider'
    let apiParams = config.apiParams || null
    let tiles = config.data || []
    let opt = config.options || {}
    let tileLayers = this.tileLayers
    let layerId = config.layerId
    let layerInfo = tileLayers[layerId] = {
      tiles: []
    }
    if (tiles.length > 0) {
      tiles.forEach((p) => {
        p.url = p.url ? p.url : p.value
        p.options = Object.assign({}, opt, p.options)
        if (p.key) {
          layerInfo.tiles.push(map.addProvider(p.key))
        } else if (p.url) {
          layerInfo.tiles.push(map.addProvider(p))
        }
      })
    }
    if (apiMethod !== null && apiMethod.length > 0 && typeof (Vue.$api[apiMethod]) === 'function' && apiParams !== null) { // 默认使用mapProvider接口，有配置接口参数时，请求接口进行数据叠加
      Vue.$api[apiMethod]({
        params: apiParams
      }).then(function (resp) {
        resp = (resp || {}).data || {}
        tiles = resp.data || []
        tiles.forEach((p, i) => {
          p.tid = p.rid || i
          p.url = p.url ? p.url : p.value
          p.options = Object.assign({}, opt, p.options)
          if (p.key) {
            layerInfo.tiles.push(map.addProvider(p.key))
          } else if (p.url) {
            layerInfo.tiles.push(map.addProvider(p))
          }
        })
      })
    }
    tileLayers.count++
  },

  /* ----------------------------------------------------瓦片图层相关操作-end---------------------------------------------------------------- */

  /* ----------------------------------------------------shp图层相关操作-start---------------------------------------------------------------- */
  /**
   * [addShpLayer 叠加shp图层]
   * @param {[type]} config [description]
   */
  addShpLayer: function (config) {
    config = this.getLayerConfig(config) || []
    let map = this.getMap()
    if (map === null) {
      return
    }
    config.forEach((c) => {
      let shps = c.data || []
      let shpLayers = this.shpLayers
      let layerId = c.layerId
      let layerInfo = shpLayers[layerId] = {
        shps: [],
        layer: map.layer()
      }
      if (shps.length > 0) {
        shps.forEach((p) => {
          let shpUrl = p.url ? p.url : p.value
          if (shpUrl) {
            layerInfo.shps.push(layerInfo.layer.addShp(shpUrl, c.properties))
          }
        })
      }
    })
  },

  /* ----------------------------------------------------shp图层相关操作-end---------------------------------------------------------------- */
  /* ----------------------------------------------------图片图层相关操作-start---------------------------------------------------------------- */
  addImageLayer: function (config) {
    config = this.getLayerConfig(config)
    let len = config.length
    let temp
    for (let i = 0; i < len; i++) {
      temp = config[i]
      this._addImageLayer(temp)
    }
  },

  /**
   * [addImageLayer 叠加图片图层]
   * @param {[object]} config   [图层配置，结构说明同addLayer函数一致]
   * @param {[type]} autoPlay [是否自动播放图片]
   */
  _addImageLayer: function (config) {
    let map = this.getMap()
    if (map === null) {
      return
    }
    let layer = map.layer()
    let apiMethod = config.apiMethod || null
    let apiParams = config.apiParams || {}
    let data = config.data || []
    let props = config.properties || {}
    let imageLayers = this.imageLayers
    let layerId = config.layerId
    let layerInfo = imageLayers[layerId] = {
      layer: layer,
      data: [],
      images: []
    }
    if (data.length > 0) { // 有配置静态数据时，优先叠加静态数据
      layerInfo.data = data
      data.forEach((d) => {
        layerInfo.images.push(layer.addImage(Object.assign({}, props), d))
      })
    }
    if (apiMethod !== null && apiMethod.length > 0 && typeof (Vue.$api[apiMethod]) === 'function') { // 有配置接口时，请求接口进行数据叠加
      Vue.$api[apiMethod]({
        params: apiParams
      }).then(function (resp) {
        data = (resp || {}).data || []
        if (data.length > 0) {
          layerInfo.data = layerInfo.data.concat(data)
          data.forEach((d) => {
            layerInfo.images.push(layer.addImage(Object.assign({}, props), d))
          })
        }
      })
    }
    imageLayers.count++
  },
  /* ----------------------------------------------------图片图层相关操作-end---------------------------------------------------------------- */

  /* ----------------------------------------------------移除图层相关操作-start---------------------------------------------------------------- */
  /**
   * [clear 移除所有图层]
   * @return {[type]} [description]
   */
  clear: function () {
    this._removeLayers(null, 'vectoryLayers')
    this._removeLayers(null, 'tileLayers')
    this._removeLayers(null, 'shpLayers')
    this._removeLayers(null, 'imageLayers')
  },

  /**
   * [removeLayer 移除指定layerId的所有类型的图层]
   * @param  {[type]} layerId [description]
   * @return {[type]}         [description]
   */
  removeLayer: function (layerId) {
    this._removeLayers(layerId, 'vectoryLayers')
    this._removeLayers(layerId, 'tileLayers')
    this._removeLayers(layerId, 'shpLayers')
    this._removeLayers(layerId, 'imageLayers')
  },

  /**
   * [removeVectoryLayer 移除矢量图层]
   * @param  {[type]} layerId [图层编号或者图层配置信息]
   * @return {[type]}          [description]
   */
  removeVectoryLayer: function (layerId) {
    this._removeLayers(layerId, 'vectoryLayers')
  },
  removeTileLayer: function (layerId) {
    this._removeLayers(layerId, 'tileLayers')
  },
  removeShpLayer: function (layerId) {
    this._removeLayers(layerId, 'shpLayers')
  },
  removeImageLayer: function (layerId) {
    this._removeLayers(layerId, 'imageLayers')
  },
  _removeLayers: function (layerId, layerType) {
    // Popup.closeAll()
    let that = this
    let configs = that.getLayerConfig(layerId) || null
    let layers = that[layerType] || {}
    let l = null
    if (configs === null || configs.length === 0) {
      for (let key in layers) {
        l = layers[key] || {}
        if (that._removeLayer(l)) {
          layers[key] = null
        }
      }
    } else {
      configs.forEach((c) => {
        if (c && c.layerId) {
          l = layers[c.layerId] || {}
          if (that._removeLayer(l)) {
            layers[c.layerId] = null
          }
        }
      })
    }
  },
  _removeLayer: function (layerInfo) {
    let shps = null
    let tiles = null
    let images = null
    let res = false
    tiles = layerInfo.tiles || []
    if (tiles.length > 0) {
      tiles.forEach((t) => {
        t.remove()
        res = true
      })
    }
    shps = layerInfo.shps || []
    if (shps.length > 0) {
      shps.forEach((t) => {
        t.remove()
        res = true
      })
    }
    images = layerInfo.images || []
    if (images.length > 0) {
      images.forEach((img) => {
        img.remove()
        res = true
      })
    }
    if (layerInfo.layer) {
      layerInfo.layer.clear()
      layerInfo.layer.remove()
      res = true
    }
    return res
  }
  /* ----------------------------------------------------移除图层相关操作-end---------------------------------------------------------------- */
}

export {
  LayerManager,
  MapUtil
}
export default MapUtil
