import DataSort from './sort'
import DataFilter from './filter'
import DataMerge from './merge'

import Complex from './complex'

export default {
  // 一组数据处理
  DataSort,
  DataFilter,
  DataFormat: (data, configObject = {}, groupAttrName, type = 'flat') => {
    return Complex(data, configObject, groupAttrName, 'data_format', type)
  },
  DataReplace: (data, configObject = {}, groupAttrName, type = 'flat') => {
    return Complex(data, configObject, groupAttrName, 'data_replace', type)
  },
  DataCombine: (data, configObject = {}, groupAttrName, type = 'flat') => {
    return Complex(data, configObject, groupAttrName, 'data_combine', type)
  },
  DataFollowTip: (data, configObject = {}, groupAttrName, type = 'flat') => {
    return Complex(data, configObject, groupAttrName, 'data_follow_tip', type)
  },
  DataCalculate: (data, configObject = {}, groupAttrName, type = 'flat') => {
    return Complex(data, configObject, groupAttrName, 'data_calculate', type)
  },
  DataAddAttributes: (data, configObject = {}, groupAttrName, type = 'flat') => {
    return Complex(data, configObject, groupAttrName, 'data_add_attributes', type)
  },
  // 多组数据合并
  DataMerge
}
